import React, { Component } from "react";
import axios from "axios";
import FormTerimaSupplier from "./Component/FormTerimaSupplier";
import { connect } from "react-redux";
import { getUUID } from "../actions/fakturAction";

const defaultSorted = [
  {
    dataField: "city",
    order: "asc",
  },
];

const server = process.env.REACT_APP_BACKEND_URL;
const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
  };
};

class TolakanReturnSupplier extends Component {
  constructor(props) {
    var date = new Date();
    var formatedDate = `${date.getFullYear()}-${
      date.getMonth() + 1
    }-${date.getDate()}`;
    super(props);
    this.state = {
      supplierList: [],
      DataSupplier: [],
      startDate: formatedDate,
      kodeSupplier: "",
      namaSupplier: "",
      tanggal: formatedDate,
      noBon: "",
      status: "",
      tampungBarang: [],
    };
  }

  componentDidMount() {
    axios
      .get(server + "suppliers")
      .then((res) =>
        this.setState({
          supplierList: res.data,
        })
      )
      .catch((err) => alert("ERROR GET DATA"));
    this.props.dispatch(getUUID());
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  pilihSupplier = (input) => (e) => {
    var supp = e.target.value ? e.target.value : "DEFALT | DEFAULT";
    var result_explode = supp.split("|");
    this.setState({
      kodeSupplier: result_explode[0],
      namaSupplier: result_explode[1],
    });
  };

  handleSubmit(data) {
    let isi = {
      noFaktur: this.props.noFaktur,
      tanggal: this.state.tanggal,
      noBon: this.state.noBon,
      kodeSupplier: this.state.kodeSupplier,
      namaSupplier: this.state.namaSupplier,
      tanggalBarang: "-",
      barangDetails: {
        noFaktur: this.props.noFaktur, //
        nettoDiscGlobalExt: 0,
        gross: parseFloat(data.gross), //berat + bungkus
        netto: parseFloat(data.netto), //murni
        qty: 0,
        k: parseFloat(data.k),
        rp: parseFloat(data.rp), //ongkos
        kadarCetak: 0,
        keterangan: "-",
        discGlobalPr: 0,
        kodeKategori: "-",
        kadarTransaksi: parseFloat(data.kadarTransaksi), //harga%
        kodeJenis: data.kodeJenis, //kodebarang
        noBarcode: "-",
        p: parseFloat(data.p),
        discGlobalGr: 0,
        pkg: parseFloat(data.pkg),
        bruto: parseFloat(data.bruto), //berat
        nettoDiscGlobal: 0,
      },
    };
    console.log(isi);
    var array = this.state.tampungBarang;
    array.push(isi);
    this.setState({
      tampungBarang: array,
    });
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <div className="col-lg-12 col-md-12">
                  <div className="row">
                    <div className="col-lg-4 col-md-4">
                      <label htmlFor="">Supplier</label>
                      <select
                        name="supplier"
                        id="supplier"
                        className="form-control"
                        defaultValue="DEFAULT | DEFAULT"
                        onChange={this.pilihSupplier()}
                      >
                        {this.state.supplierList == [] ? (
                          <option>DATA KOSONG</option>
                        ) : (
                          this.state.supplierList.map((list) => (
                            <option
                              value={
                                list.kodeSupplier + "|" + list.namaSupplier
                              }
                            >
                              {list.namaSupplier}
                            </option>
                          ))
                        )}
                      </select>
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Tanggal</label>
                      <input
                        type="date"
                        id="tanggal"
                        name="tanggal"
                        className="form-control"
                        defaultValue={this.state.startDate}
                        onChange={this.inputChange("tanggal")}
                      />
                    </div>
                    <div className="col-md-4 col-lg-4 ">
                      <label htmlFor="">No Bon</label>
                      <input
                        type="text"
                        id="bon_return"
                        name="bon_return"
                        className="form-control"
                        onChange={this.inputChange("noBon")}
                      />
                    </div>
                    <div className="col-md-12 col-lg-12 mt-3">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          data-toggle="modal"
                          data-target="#primary-header-modal"
                        >
                          Tambah Data
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Tolakan Return Supplier
                </h4>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  Ã—
                </button>
              </div>
              <div className="modal-body">
                <FormTerimaSupplier
                  from="tolakan"
                  onSubmit={(data) => this.handleSubmit(data)}
                  noFaktur={this.props.noFaktur}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(TolakanReturnSupplier);
